.header{
    top: 0;
    position: sticky;
    display: flex;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgray;
    padding: 10px 0;
    width: 100%;
    z-index: 999;
    background-color: white;
}
.header__left{
    display: flex;
}
.header__left > img {
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
}

.header__search{
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    height: 22px;
    color:gray;
    background-color: #eef3f8;
}

.header__search > input {
    outline: none;
    border: none;
    background: none;
}

.header__right{
    display: flex;

}

@media(max-width: 770px){
    .header__left{
        width: 100%;
        padding: 0 20px;
    }
    .header__search{
        width: 80%;
    }
}