.login{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 0 auto;
    padding: 100px 0;
    align-items: center;
}
.login > img {
    object-fit: contain;
    height: 120px;
    margin: 20px 0;
}

.login > form {
    display: flex;flex-direction: column;
}

.login > form > input {
    width: 350px;
    height: 50px;
    font-size: 20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.login > form > button {
    width: 365px;
    height: 50px;
    font-size: large;
    color: #fff;
    background-color: #0074b1;
    border-radius: 5px;
    cursor: pointer;
}

.login__register{
    color: #0177b7;
    cursor: pointer;
}
